import React, { useState } from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TermsTable from "./TermsTable";

import PDFPage from "./PDFPage";
import MovementPrep from "./MovementPrep";
import MentalFitness from "./MentalFitness";
import SleepRecovery from "./SleepRecovery";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function ResourcesTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="Terms" {...a11yProps(0)} />
          <Tab label="Movement Preparation Guide" {...a11yProps(1)} />
          <Tab label="Mental Fitness" {...a11yProps(2)} />
          <Tab label="Sleep Recovery" {...a11yProps(3)} />
        </Tabs>
      </Box>

      <TabPanel value={value} index={0}>
        <TermsTable />
      </TabPanel>

      <TabPanel value={value} index={1}>
        <MovementPrep />
      </TabPanel>

      <TabPanel value={value} index={2}>
        <MentalFitness />
      </TabPanel>

      <TabPanel value={value} index={3}>
        <SleepRecovery />
      </TabPanel>
    </Box>
  );
}
